import React, { Fragment, useState } from 'react';
import { Dialog, Transition } from '@headlessui/react';
import { Link, useLocation, Outlet } from 'react-router-dom';
import {
  Bars3Icon,
  XMarkIcon,
  HomeIcon,
  UsersIcon,
  CurrencyRupeeIcon,
  ArrowsRightLeftIcon,
  ChartBarIcon,
  UserCircleIcon,
  UserGroupIcon,
  DocumentTextIcon,
  ChatBubbleLeftRightIcon
} from '@heroicons/react/24/outline';
import Logo from './Logo';

const navigation = [
  { name: 'Dashboard', href: '/', icon: HomeIcon },
  { name: 'Customers', href: '/customers', icon: UsersIcon },
  { name: 'Transactions', href: '/transactions', icon: ArrowsRightLeftIcon },
  { name: 'Add Payment', href: '/payments/add', icon: CurrencyRupeeIcon },
  { name: 'Reports', href: '/reports', icon: ChartBarIcon },
  { name: 'WhatsApp Settings', href: '/whatsapp-settings', icon: ChatBubbleLeftRightIcon },
];

const pageTitle = {
  '/': 'Dashboard',
  '/customers': 'Customers',
  '/transactions': 'Transactions',
  '/payments/add': 'Add Payment',
  '/reports': 'Reports',
};

function classNames(...classes) {
  return classes.filter(Boolean).join(' ');
}

export default function Layout() {
  const [sidebarOpen, setSidebarOpen] = useState(false);
  const location = useLocation();
  const currentPath = '/' + location.pathname.split('/')[1]; // Handle nested routes

  const renderNavLinks = () => (
    <ul role="list" className="-mx-2 space-y-1">
      {navigation.map((item) => {
        const isActive = 
          item.href === '/' 
            ? currentPath === '/' 
            : currentPath.startsWith(item.href);
            
        return (
          <li key={item.name}>
            <Link
              to={item.href}
              className={classNames(
                isActive
                  ? 'bg-primary-50 text-primary-600'
                  : 'text-gray-700 hover:text-primary-600 hover:bg-primary-50',
                'group flex gap-x-3 rounded-md p-2 text-sm leading-6 font-medium'
              )}
              onClick={() => setSidebarOpen(false)}
            >
              <item.icon
                className={classNames(
                  isActive ? 'text-primary-600' : 'text-gray-400 group-hover:text-primary-600',
                  'h-5 w-5 flex-shrink-0'
                )}
                aria-hidden="true"
              />
              {item.name}
            </Link>
          </li>
        );
      })}
    </ul>
  );

  return (
    <div className="min-h-screen bg-gray-50">
      {/* Mobile sidebar */}
      <Transition.Root show={sidebarOpen} as={Fragment}>
        <Dialog as="div" className="relative z-50 lg:hidden" onClose={setSidebarOpen}>
          <Transition.Child
            as={Fragment}
            enter="transition-opacity ease-linear duration-300"
            enterFrom="opacity-0"
            enterTo="opacity-100"
            leave="transition-opacity ease-linear duration-300"
            leaveFrom="opacity-100"
            leaveTo="opacity-0"
          >
            <div className="fixed inset-0 bg-gray-900/80" />
          </Transition.Child>

          <div className="fixed inset-0 flex">
            <Transition.Child
              as={Fragment}
              enter="transition ease-in-out duration-300 transform"
              enterFrom="-translate-x-full"
              enterTo="translate-x-0"
              leave="transition ease-in-out duration-300 transform"
              leaveFrom="translate-x-0"
              leaveTo="-translate-x-full"
            >
              <Dialog.Panel className="relative mr-16 flex w-full max-w-xs flex-1">
                <div className="absolute left-full top-0 flex w-16 justify-center pt-5">
                  <button type="button" className="-m-2.5 p-2.5" onClick={() => setSidebarOpen(false)}>
                    <span className="sr-only">Close sidebar</span>
                    <XMarkIcon className="h-5 w-5 text-white" aria-hidden="true" />
                  </button>
                </div>
                <div className="flex grow flex-col gap-y-5 overflow-y-auto bg-white px-6 pb-4">
                  <div className="flex h-14 shrink-0 items-center">
                    <Link to="/" className="flex items-center gap-3">
                      <Logo className="h-8 w-8" />
                      <span className="text-lg font-semibold text-gray-900">WhatsApp Accounting</span>
                    </Link>
                  </div>
                  <nav className="flex flex-1 flex-col">
                    <ul role="list" className="flex flex-1 flex-col gap-y-7">
                      <li>{renderNavLinks()}</li>
                    </ul>
                  </nav>
                </div>
              </Dialog.Panel>
            </Transition.Child>
          </div>
        </Dialog>
      </Transition.Root>

      {/* Static sidebar for desktop */}
      <div className="hidden lg:fixed lg:inset-y-0 lg:z-50 lg:flex lg:w-64 lg:flex-col">
        <div className="flex grow flex-col gap-y-5 overflow-y-auto border-r border-gray-200 bg-white px-6 pb-4">
          <div className="flex h-16 shrink-0 items-center">
            <Link to="/" className="flex items-center gap-3">
              <Logo className="h-8 w-8" />
              <span className="text-lg font-semibold text-gray-900">WhatsApp Accounting</span>
            </Link>
          </div>
          <nav className="flex flex-1 flex-col">
            <ul role="list" className="flex flex-1 flex-col gap-y-7">
              <li>{renderNavLinks()}</li>
            </ul>
          </nav>
        </div>
      </div>

      <div className="lg:pl-64">
        {/* Top bar */}
        <div className="sticky top-0 z-40 flex h-16 shrink-0 items-center justify-between gap-x-4 border-b border-gray-200 bg-white px-4 shadow-sm sm:gap-x-6 sm:px-6 lg:px-8">
          <button
            type="button"
            className="-m-2.5 p-2.5 text-gray-700 lg:hidden"
            onClick={() => setSidebarOpen(true)}
          >
            <span className="sr-only">Open sidebar</span>
            <Bars3Icon className="h-5 w-5" aria-hidden="true" />
          </button>

          <div className="flex flex-1 items-center justify-between">
            <h1 className="text-lg font-semibold leading-7 text-gray-900">
              {pageTitle[currentPath] || 'Dashboard'}
            </h1>
            <div className="flex items-center gap-x-4">
              <div className="flex items-center gap-x-3">
                <span className="text-sm font-medium text-gray-500">John Doe</span>
                <div className="h-8 w-8 rounded-full bg-gray-100 flex items-center justify-center text-gray-500">
                  <UserCircleIcon className="h-6 w-6" aria-hidden="true" />
                </div>
              </div>
            </div>
          </div>
        </div>

        <main className="py-8">
          <div className="px-4 sm:px-6 lg:px-8">
            <Outlet />
          </div>
        </main>
      </div>
    </div>
  );
} 